import { AppAssets } from "app/shared/data/app-assets";
import { Component, Input, OnInit } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { IMyCoursesMV } from "app/admin/models/admin/setup/course";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { map } from "rxjs/operators";

@Component({
  selector: "course-expansion-panel",
  templateUrl: "./course-expansion-panel.component.html",
  styleUrls: ["./course-expansion-panel.component.scss"],
})
export class CourseExpansionPanelComponent implements OnInit {
  @Input() courses: IMyCoursesMV[];
  curLang: string;
  constructor(
    private translate: TranslateService,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {}
  ngOnChanges() {
    this.courses?.map((ele) => {
      ele.course_Minutes = ele.courseMinutes % 60;
      ele.course_hours = (ele.courseMinutes - ele.course_Minutes) / 60;
    });
  }
}
