import { Component, OnInit } from "@angular/core";
import { IMyDiplomasMV } from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-diplomas",
  templateUrl: "./diplomas.component.html",
  styleUrls: ["./diplomas.component.scss"],
})
export class DiplomasComponent implements OnInit {
  diplomas: IMyDiplomasMV[];
  userId: number;

  constructor(
    private courseService: CourseService,
    private spinner: NgxSpinnerService,
    private generalService: GeneralService
  ) {
    this.generalService.userData.subscribe((user) => {
      if (this.userId != user?.user.user_ID) {
        this.userId = user?.user.user_ID;
        this.getAllDiplomas();
      }
    });
  }

  ngOnInit(): void {}

  getAllDiplomas() {
    this.spinner.show();
    this.courseService
      .getAllDiploma(this.userId)
      .subscribe((res: IMyDiplomasMV[]) => {
        this.diplomas = res;
        this.spinner.hide();
      });
  }
}
