import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { INotificationModel } from "../models/notifications";

@Injectable({
  providedIn: "root",
})
export class NotificationsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  GetAll() {
    return this.get<INotificationModel[]>(`student/notifications`);
  }
}
