import { Component, OnInit } from "@angular/core";
import {
  IMyCoursesMV,
  IRecAndBasicCoursesMV,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-basic-courses",
  templateUrl: "./basic-courses.component.html",
  styleUrls: ["./basic-courses.component.scss"],
})
export class BasicCoursesComponent implements OnInit {
  basicCourses: IMyCoursesMV[];

  constructor(
    private courseService: CourseService,
    private spinner: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.getAllEmployeeBasicCourses();
  }

  getAllEmployeeBasicCourses() {
    this.spinner.show();
    this.courseService
      .getBasicAndRecommendedCourses()
      .subscribe((res: IRecAndBasicCoursesMV) => {
        this.basicCourses = res.courses.filter(
          (course) => course.basic && !course.isCurrent
        );
        this.spinner.hide();
      });
  }
}
