import { DatePipe } from "@angular/common";

// @Injectable({
// 	providedIn: "root",
// })
export class HelpersFunctions {
  static datePipe = new DatePipe("en-US");

  static transformData(date: string) {
    return this.datePipe.transform(date, "yyyy-MM-ddTHH:MM:SS");
  }

  static mapDate(date: string) {
    if (date) return this.datePipe?.transform(date, "dd-MM-yyyy");
    else return;
  }

  // get day name by date getDayName('2022-01-29') => Saturday
  static getDayName(date = new Date(), locale = "en-US") {
    return date.toLocaleDateString(locale, { weekday: "long" });
  }

  static mapDateReverse(date: string) {
    return this.datePipe.transform(date, "yyyy-MM-dd");
  }

  static mapTime(time: string) {
    if (time) {
      let timeParts = time.split(":");
      let date = new Date(
        null,
        null,
        null,
        Number(timeParts[0]),
        Number(timeParts[1] ?? 0),
        Number(timeParts[2] ?? 0)
      );
      return this.datePipe.transform(date, "hh:mm a");
    }
  }

  static numberOfDaysInMonth(year: number, month: number): number {
    // get month from 0 - 11
    const date = new Date(year, month + 1, 0);
    return date.getDate();
  }

  static convertEditorValueIntoTextOnly(editorValue: string): string {
    const element = document.createElement("div");
    element.innerHTML = editorValue;
    return element.innerText;
  }
}
