import { Component, OnDestroy, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { unit } from "app/admin/models/admin/educations";
import {
  IAllAttachmentGetVM,
  ICourseDetailsModel,
  ISolvedExamsModel,
  IUnitAttachmentResponseVM,
  IUnitDataGeneralVM,
  IUnitDataPartsVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { AppAssets } from "app/shared/data/app-assets";
import { AppStrings } from "app/shared/data/app-strings";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { MaterialsService } from "app/student/services/materials.service";
import { UserCourseService } from "app/student/services/user-course.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-my-course-content",
  templateUrl: "./my-course-content.component.html",
  styleUrls: ["./my-course-content.component.scss"],
})
export class MyCourseContentComponent implements OnInit, OnDestroy {
  courseId: number;
  units: unit[] = [];
  currentUnitData: IUnitDataGeneralVM[] = [];
  courseAttachments: IUnitAttachmentResponseVM[];
  currentUnitAttachments: IAllAttachmentGetVM[] = [];
  currentUnitGrades: ISolvedExamsModel[] = [];
  form: FormGroup;

  // exam or lecture
  selectedItem: IUnitDataGeneralVM;
  // selected videon in lecture
  selectedPartId: number;

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService,
    private spinner: NgxSpinnerService,
    public languageService: LanguageService,
    public appAssets: AppAssets,
    public appStrings: AppStrings,
    private fb: FormBuilder,
    private subjectsService: SubjectsService,
    public fileService: UploadFileService,
    private materialsService: MaterialsService,
    private toastr: MessagesService,
    private router: Router,
    private userCourseService: UserCourseService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));
      if (this.courseId > 0) {
        this.getCourseDetails();
        this.getAllStagesByCourseId();
        this.getAllCourseAttachments();
      }

      this.selectedPartId = Number(params.get("partId"));

      // let examId = Number(params.get("examId"));
      // if (examId > 0) {}
    });
  }

  ngOnInit() {
    this.form = this.fb.group({
      unitId: [null],
      unitIdInGrades: [null],
      unitIdInAttachs: [null],
    });
  }

  get formCtrl() {
    return this.form.controls;
  }

  getCourseDetails() {
    // this.spinner.show();
    this.courseService
      .getCourseDetails(this.courseId)
      .subscribe((res: ICourseDetailsModel) => {
        this.userCourseService.courseData$.next(res);
        // this.spinner.hide();
      });
  }

  //#region unit data
  getAllStagesByCourseId() {
    this.spinner.show();
    this.subjectsService.getUnits(this.courseId).subscribe((res) => {
      if (res.length > 0) {
        this.units = res;
        this.units.forEach((unit, index) => {
          this.getAllUnitData(unit.id, index);
          // select first unit
          if (index == 0) {
            let firstUnitId = this.units[index].id;
            this.formCtrl.unitId.setValue(firstUnitId);
            this.formCtrl.unitIdInGrades.setValue(firstUnitId);
            this.formCtrl.unitIdInAttachs.setValue(firstUnitId);
            this.getUnitGrades(firstUnitId);
          }
        });
        // this.spinner.hide();
        // console.log(this.units);
      } else this.spinner.hide();
    });
  }

  // return data in stage (unit)
  getAllUnitData(unitId: number, index: number) {
    this.courseService.getAllUnitData(unitId).subscribe((res) => {
      if (res) {
        // exams & lectures
        let unitData: IUnitDataGeneralVM[] = res.model;
        unitData = unitData.sort((a, b) => a.order - b.order);
        unitData.map((ele) => {
          // if (ele.type == this.appStrings.stageExam) {
          //   ele.exam_minutes = ele.period_minute % 60;
          //   ele.exam_hour = (ele.period_minute - ele.exam_minutes) / 60;
          // }
          ele.unitId = unitId;
        });
        this.units[index].data = unitData;

        // after return of last unit data
        if (index == this.units.length - 1)
          setTimeout(() => {
            this.getFirstSelectedItem();
          }, 1000);
        // console.log("unit data", unitData);
      }
      this.spinner.hide();
    });
  }

  getFirstSelectedItem() {
    if (this.selectedPartId > 0) {
      // get lecture of selected part
      this.units.forEach((unit, unitIndex) => {
        unit.data.forEach((lecture) => {
          if (lecture.type == this.appStrings.stageLecture) {
            lecture.parts.forEach((part) => {
              if (part.id == this.selectedPartId) {
                this.formCtrl.unitId.setValue(unit.id);
                this.currentUnitData = this.units[unitIndex].data;
                this.selectedItem = lecture;
              }
            });
          }
        });
      });
    } else {
      let selectedUnitIndex = this.getUnitIndex(this.formCtrl.unitId.value);
      this.currentUnitData = this.units[selectedUnitIndex].data;
      this.selectedItem = this.units[selectedUnitIndex].data[0];
      if (this.selectedItem.type == this.appStrings.stageLecture)
        this.selectedPartId = this.selectedItem.parts[0].id;
    }
  }
  //#endregion

  //#region attaches & grades
  getSelectedUnitData(unitId: number) {
    let index = this.getUnitIndex(unitId);
    this.currentUnitData = this.units[index].data;
  }

  getAllCourseAttachments() {
    this.courseService.getAllUnitAttachments(this.courseId).subscribe((res) => {
      if (res) {
        this.courseAttachments = res.UnitData.reverse();
        this.currentUnitAttachments = this.courseAttachments[0].FileEduUnit;
      }
    });
  }

  getUnitAttach(unitId: number) {
    let index = this.courseAttachments.findIndex((unit) => unit.id == unitId);
    if (index >= 0)
      this.currentUnitAttachments = this.courseAttachments[index].FileEduUnit;
    else this.currentUnitAttachments = [];
  }

  getUnitGrades(unitId: number) {
    this.courseService.getAllUnitGrades(unitId).subscribe((res) => {
      if (res) this.currentUnitGrades = res;
    });
  }
  //#endregion

  //#region no of data & index
  examIndexInUnit(examId: number): number {
    let exams = this.currentUnitData.filter(
      (ele) => ele.type == this.appStrings.stageExam
    );
    return exams.findIndex((ele) => ele.id == examId) + 1;
  }

  lectureIndexInUnit(lectureId: number): number {
    let lectures = this.currentUnitData.filter(
      (ele) => ele.type == this.appStrings.stageLecture
    );
    return lectures.findIndex((ele) => ele.id == lectureId) + 1;
  }

  changeVideoView(lectureId: number, partId: number, checked: boolean) {
    this.spinner.show();
    // increment view && completed
    if (checked)
      this.materialsService
        .incrementView(lectureId, partId)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.toastr.success("General.AddSuccessfully");
          } else this.toastr.warning(res.returnString);
          this.spinner.hide();
        });
    // remove view && completed
    else
      this.materialsService.removeViews(partId).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
        } else this.toastr.warning(res.returnString);
        this.spinner.hide();
      });
  }
  //#endregion

  //#region open exam and videos
  openVideoPart(lecture: IUnitDataGeneralVM, part: IUnitDataPartsVM) {
    // console.log("open part", part);
    this.selectedItem = lecture;
    this.selectedPartId = part.id;
  }

  openExam(exam: IUnitDataGeneralVM) {
    // console.log("open exam", exam);
    this.selectedItem = exam;
    this.selectedPartId = 0;
  }

  checkIsAllowedToNavigate(data: IUnitDataGeneralVM, currentItemIndex: number) {
    let unitIndex = this.getUnitIndex(data.unitId);

    // previous units
    if (unitIndex > 0) {
      let lastMandatoryItemPreviousUnits: IUnitDataGeneralVM;
      for (let i = 0; i < unitIndex; i++) {
        // get last mandatory item before current (selected)
        this.units[i].data?.forEach((item) => {
          if (item.isMandatory) lastMandatoryItemPreviousUnits = item;
        });
      }

      if (
        lastMandatoryItemPreviousUnits &&
        !lastMandatoryItemPreviousUnits.isComplete
      )
        return false;
    }

    // get last mandatory item before current (selected)
    let lastMandatoryItem: IUnitDataGeneralVM;
    this.units[unitIndex].data.forEach((item, index) => {
      if (index < currentItemIndex && item.isMandatory)
        lastMandatoryItem = item;
    });

    return data.isComplete ||
      !lastMandatoryItem ||
      lastMandatoryItem?.isComplete ||
      currentItemIndex == 0
      ? true
      : false;
  }

  openNextItem() {
    // console.log("next");
    let unitIndex = this.getUnitIndex(this.selectedItem.unitId);
    let itemIndex = this.getExamAndLectureIndex(
      unitIndex,
      this.selectedItem.id
    );

    // the next part of current lecture
    if (this.selectedItem.type == this.appStrings.stageLecture) {
      let partIndex = this.selectedItem.parts.findIndex(
        (part) => part.id == this.selectedPartId
      );
      // change completed if part of lecture
      this.units[unitIndex].data[itemIndex].parts[partIndex].isCompleted = true;
      if (partIndex < this.selectedItem.parts.length - 1) {
        this.selectedPartId = this.selectedItem.parts[partIndex + 1].id;
        return;
      }
    }
    // change completed for exam and for lecture itself
    this.units[unitIndex].data[itemIndex].isComplete = true;

    // next item in the same unit
    if (itemIndex < this.units[unitIndex].data.length - 1) {
      this.selectedItem = this.units[unitIndex].data[itemIndex + 1];
      if (this.selectedItem.type == this.appStrings.stageLecture)
        this.selectedPartId = this.selectedItem.parts[0].id;
    }
    // next item in the next unit
    else if (unitIndex < this.units.length - 1) {
      let newUnitId = this.units[unitIndex + 1].id;
      this.formCtrl.unitId.setValue(newUnitId);
      this.getSelectedUnitData(newUnitId);
      this.selectedItem = this.units[unitIndex + 1].data[0];
    }
    // course ended > there is no next item
    else this.router.navigate([`/student/course-ended/${this.courseId}`]);
  }

  videoCompleted() {
    let unitIndex = this.getUnitIndex(this.selectedItem.unitId);
    let itemIndex = this.getExamAndLectureIndex(
      unitIndex,
      this.selectedItem.id
    );
    let partIndex = this.selectedItem.parts.findIndex(
      (part) => part.id == this.selectedPartId
    );
    // change original list (to use when i change unit and return to current one)
    this.units[unitIndex].data[itemIndex].parts[partIndex].isCompleted = true;

    // // to change current checkbox
    // if (this.currentUnitData[0].unitId == this.selectedItem.unitId)
    //   this.currentUnitData[itemIndex].parts[partIndex].isCompleted = true;
  }
  //#endregion

  //#region helper functions
  getUnitIndex(unitId: number): number {
    return this.units.findIndex((unit) => unit.id == unitId);
  }

  getExamAndLectureIndex(unitIndex: number, itemId: number): number {
    return this.units[unitIndex].data.findIndex((data) => data.id == itemId);
  }
  //#endregion

  ngOnDestroy(): void {
    this.userCourseService.courseData$.next(null);
    // this.userCourseService.courseData$.complete();
  }
}
