import { Component, OnInit } from "@angular/core";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { INotificationModel } from "app/student/models/notifications";
import { NotificationsService } from "app/student/services/notifications.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-notifications",
  templateUrl: "./notifications.component.html",
  styleUrls: ["./notifications.component.scss"],
})
export class NotificationsComponent implements OnInit {
  notificationList: INotificationModel[] = [];

  constructor(
    private notificationsService: NotificationsService,
    public languageService: LanguageService,
    private spinner: NgxSpinnerService,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {}

  ngOnInit() {
    // this.getNotifications();
    // this.notificationList = [
    //   {
    //     ar_name: "asd",
    //     en_name: "adscx addsx awdawdsad adwawsd adasdx adfas",
    //     attach_path: null,
    //     noOfVideos: 20,
    //     type: "update",
    //   },
    //   {
    //     ar_name: "asd",
    //     en_name: "adscx addsx awdawdsad adwawsd adasdx adfas",
    //     attach_path: null,
    //     noOfVideos: 0,
    //     type: "approved df",
    //   },
    //   {
    //     ar_name: "asd",
    //     en_name: "adscx addsx awdawdsad adwawsd adasdx adfas ads afdcsxz",
    //     attach_path: null,
    //     noOfVideos: 0,
    //     type: "rejected xdgf",
    //   },
    // ];
  }

  getNotifications() {
    this.spinner.show();
    this.notificationsService
      .GetAll()
      .subscribe((res: INotificationModel[]) => {
        this.notificationList = res;
        this.spinner.hide();
      });
  }

  getTypeClass(type: string): string {
    if (type.toLocaleLowerCase().includes("update")) return "info";
    else if (type.toLocaleLowerCase().includes("approve")) return "success";
    else if (type.toLocaleLowerCase().includes("reject")) return "danger";
  }
}
