import { AuthService } from "app/shared/services/auth/auth.service";
import { Component, OnInit } from "@angular/core";
import { CourseAndTeachingRequestService } from "app/admin/services/Admin/course-teaching-request.service";
import {
  CourseData,
  IAddCourseRequest,
} from "app/admin/models/admin/course-request";
import { MatDialog } from "@angular/material/dialog";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";
import { NgxSpinnerService } from "ngx-spinner";
import { OwlOptions } from "ngx-owl-carousel-o";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { AddCourseRequestDetailsComponent } from "../add-course-request-details/add-course-request-details.component";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { CategoriesModel } from "app/admin/models/setup/categories";

////////////////////////////////////////////// not used in new ui

@Component({
  selector: "app-course-employee-request",
  templateUrl: "./course-employee-request.component.html",
  styleUrls: ["./course-employee-request.component.scss"],
})
export class CourseEmployeeRequestComponent implements OnInit {
  categories: CategoriesModel[] = [];
  selectedCategory: number = 0;
  coursesList: CourseData[] = [];
  totalItems: number = 0;
  EduCompId: number;
  selectedCategoryId: number;

  constructor(
    private courseAndTeachingRequestService: CourseAndTeachingRequestService,
    private categoriesService: CategoriesService,
    public languageService: LanguageService,
    private dialog: MatDialog,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService,
    private authService: AuthService,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {
    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllCategories();
  }

  getAllCategories() {
    // this.spinner.show();
    // this.categoriesService.getCategoriesForEmployee().subscribe((res) => {
    //   if (res.returnValue == 1) this.categories = res.courseCategoryList;
    //   this.spinner.hide();
    // });
  }

  getAllCourses(categoryId: number) {
    this.selectedCategoryId = categoryId;
    // console.log(departmentId);
    this.spinner.show();
    this.courseAndTeachingRequestService
      .getCoursesByCategoryIdWithEmployeeStatus(categoryId)
      .subscribe((res) => {
        if (res) {
          this.coursesList = res;
          this.selectedCategory = categoryId;
        }
        this.spinner.hide();
      });
  }

  calcCourseHours(course: CourseData) {
    // Math.floor(course.subCourseMinutes / 60)
    // course.subCourseMinutes % 60
    return Math.floor(course.courseTime / 60);
  }

  AddRequest(addDetails: IAddCourseRequest) {
    this.spinner.show();
    this.courseAndTeachingRequestService
      .addCourseRequest(addDetails)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.SendSuccessfully");
          // change current card status (remove button)
          let courseIndex = this.coursesList.findIndex(
            (course) => course.id == addDetails.courseId
          );
          if (courseIndex >= 0) {
            this.coursesList[courseIndex].hasRequestBefore = true;
            this.coursesList[courseIndex].approve = null;
          }
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openAddRquestDetailsDialog(course: CourseData): void {
    this.dialog
      .open(AddCourseRequestDetailsComponent, {
        data: {
          course: course,
        },
      })
      .afterClosed()
      .subscribe((addDetails) => {
        if (addDetails) this.AddRequest(addDetails);
      });
  }

  customOptions: OwlOptions = {
    autoplay: false,
    loop: false,
    rewind: true,
    margin: 10,
    dots: true,
    navSpeed: 300,
    navText: [
      '<i class="fas fa-arrow-right"></i>',
      '<i class="fas fa-arrow-left"></i>',
    ],
    responsive: {
      0: {
        items: 2,
      },
      300: {
        items: 3,
      },
      500: {
        items: 5,
      },
      740: {
        items: 6,
      },
      940: {
        items: 8,
      },
    },
    nav: false,
  };
}
