import {Component, OnInit} from "@angular/core";
import {MatDialog} from "@angular/material/dialog";
import {
  IUserPointsVM,
  giftGalleryViewModel,
} from "app/admin/models/admin/gifts";
import {GiftsService} from "app/admin/services/Admin/gifts.service";
import {AppAssets} from "app/shared/data/app-assets";
import {UploadFileService} from "app/shared/services/General/upload-file.service";
import {LanguageService} from "app/shared/services/language.service";
import {BuyGiftDialogComponent} from "../buy-gift-dialog/buy-gift-dialog.component";
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: "app-gift-gallery",
  templateUrl: "./gift-gallery.component.html",
  styleUrls: ["./gift-gallery.component.scss"],
})
export class GiftGalleryComponent implements OnInit {
  // itemsCount: number = 0;
  // currentLang: string;
  // giftBuy:giftViewModel[]=[];
  // allPoints: number;
  // usedPoints: number;
  availablePoints: number;
  giftList: giftGalleryViewModel[] = [];
  functionId: number;
  isLoaded: boolean = false;

  constructor(
    private giftServ: GiftsService,
    public languageService: LanguageService,
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    private dialog: MatDialog,
    private route: ActivatedRoute
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit() {
    this.getGiftGallery();
    this.giftServ.getPoints();

    this.giftServ?.userPoints.subscribe((point: IUserPointsVM) => {
      // this.allPoints = point.Allpoints;
      // this.usedPoints = point.Usedpoints;
      this.availablePoints = point.UnUsedpoints;
    });
  }

  ngAfterViewInit() {
    this.giftServ.getPoints();
    this.getGiftGallery();
  }

  getGiftGallery() {
    this.giftServ.getGiftGallery().subscribe((res: any) => {
      this.giftList = res.giftGallery;
      this.isLoaded = true;
    });
  }

  reGet() {
    this.getGiftGallery();
    this.giftServ.getPoints();
  }

  openBuyDialog(gift: giftGalleryViewModel) {
    this.dialog
      .open(BuyGiftDialogComponent, {
        data: {
          gift: gift,
        },
        maxWidth: "700px",
        minWidth: "50vw",
        autoFocus: false,
        panelClass: "dialog-radius",
      })
      .afterClosed()
      .subscribe((hasUpdate) => {
        if (hasUpdate) {
          this.getGiftGallery();
          this.giftServ.getPoints();
        }
      });
  }
}
