import { Component, OnInit } from "@angular/core";
import {
  IMyCoursesMV,
  IMyDiplomasMV,
} from "app/admin/models/admin/setup/course";
import { MessagesService } from "app/shared/services/messages.service";
import { IFavoriteResponse } from "app/student/models/favorite";
import { FavoriteCourseDiplomaService } from "app/student/services/favorite-course-diploma.service";

@Component({
  selector: "app-favorite-courses",
  templateUrl: "./favorite-courses.component.html",
  styleUrls: ["./favorite-courses.component.scss"],
})
export class FavoriteCoursesComponent implements OnInit {
  favoriteCourses: IMyCoursesMV[];
  favoriteDiplomas: IMyDiplomasMV[];
  isLoaded: boolean = false;

  constructor(
    private favoriteCourseDiplomaService: FavoriteCourseDiplomaService,
    private toastr: MessagesService
  ) {}

  ngOnInit(): void {
    this.getAllFavoriteCourses();
  }

  getAllFavoriteCourses() {
    this.favoriteCourseDiplomaService
      .GetAll()
      .subscribe((res: IFavoriteResponse) => {
        if (res.returnValue == 1) {
          res.FavoriteCourses.map(
            (course: IMyCoursesMV) => (course.isFavorite = true)
          );
          this.favoriteCourses = res.FavoriteCourses;

          res.FavoriteDiplomas.map((diploma: IMyDiplomasMV) => {
            diploma.isFavorite = true;
            diploma.diploma_ar_name = diploma.ar_name;
            diploma.diploma_en_name = diploma.Diploma_en_name;
          });
          this.favoriteDiplomas = res.FavoriteDiplomas;

          this.isLoaded = true;
        } else this.toastr.error(res.returnString);
      });
  }

  removeCourseFavorite(course: IMyCoursesMV) {
    let index = this.favoriteCourses.findIndex(
      (ele) => ele.subject_id == course.subject_id
    );
    this.favoriteCourses.splice(index, 1);
  }

  removeDiplomaFavorite(diploma: IMyDiplomasMV) {
    let index = this.favoriteDiplomas.findIndex((ele) => ele.id == diploma.id);
    this.favoriteDiplomas.splice(index, 1);
  }
}
