import { Component, OnInit } from "@angular/core";
import {
  IUserPointsVM,
  giftGalleryViewModel,
} from "app/admin/models/admin/gifts";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-gifts-history",
  templateUrl: "./gifts-history.component.html",
  styleUrls: ["./gifts-history.component.scss"],
})
export class GiftsHistoryComponent implements OnInit {
  usedPoints: number;
  giftList: giftGalleryViewModel[] = [];
  isLoaded: boolean = false;

  constructor(
    private giftServ: GiftsService,
    public languageService: LanguageService,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {}

  ngOnInit() {
    this.getGiftGallery();
    this.giftServ.getPoints();

    this.giftServ?.userPoints.subscribe((point: IUserPointsVM) => {
      this.usedPoints = point.Usedpoints;
    });
  }

  ngAfterViewInit() {
    this.giftServ.getPoints();
    this.getGiftGallery();
  }

  getGiftGallery() {
    this.giftServ.getGiftHistory().subscribe((res: giftGalleryViewModel[]) => {
      this.giftList = res;
      this.isLoaded = true;
    });
  }
}
