import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { IAddCourseRequest } from "app/admin/models/admin/course-request";
import { unit } from "app/admin/models/admin/educations";
import {
  ICourseDetailsModel,
  IUnitDataGeneralVM,
} from "app/admin/models/admin/setup/course";
import { CourseAndTeachingRequestService } from "app/admin/services/Admin/course-teaching-request.service";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { AppAssets } from "app/shared/data/app-assets";
import { AppStrings } from "app/shared/data/app-strings";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { IFavoriteRequest } from "app/student/models/favorite";
import { FavoriteCourseDiplomaService } from "app/student/services/favorite-course-diploma.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-course-details",
  templateUrl: "./course-details.component.html",
  styleUrls: ["./course-details.component.scss"],
})
export class CourseDetailsComponent implements OnInit {
  courseId: number;
  course: ICourseDetailsModel;
  seeMoreWhatWillLearn: boolean = false;
  units: unit[] = [];

  myForm: FormGroup;
  submitted: boolean = false;

  helpersFunctions = HelpersFunctions;

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService,
    private spinner: NgxSpinnerService,
    public languageService: LanguageService,
    public appAssets: AppAssets,
    public fileService: UploadFileService,
    private favoriteCourseDiplomaService: FavoriteCourseDiplomaService,
    private toastr: MessagesService,
    private fb: FormBuilder,
    private courseAndTeachingRequestService: CourseAndTeachingRequestService,
    private subjectsService: SubjectsService,
    public appStrings: AppStrings
  ) {
    this.route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));
      if (this.courseId > 0) {
        this.getCourseDetails();
        this.getAllStagesByCourseId();
      }
    });
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      reason: [null, [Validators.required, CustomeValidator.whiteSpace]],
      durationExpected: [null, [Validators.required, CustomeValidator.bigZero]],
    });
  }

  get formCtrl() {
    return this.myForm.controls;
  }

  getCourseDetails() {
    // this.spinner.show();
    this.courseService
      .getCourseDetails(this.courseId)
      .subscribe((res: ICourseDetailsModel) => {
        this.course = res;
        // this.spinner.hide();
      });
  }

  getCategoresTooltip() {
    let category: String[] = [];
    this.course.course_category.forEach((ele, index) => {
      if (index != 0) category.push(ele.name);
    });
    return category.join(", ");
  }

  changeCourseFavorite() {
    this.spinner.show();
    // delete favorite
    if (this.course.isFavorite) {
      this.favoriteCourseDiplomaService
        .Delete(this.course.id)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.course.isFavorite = false;
            this.toastr.success("General.DeleteSuccessfully");
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    }
    // add favorite
    else {
      let model: IFavoriteRequest = {
        course_id: this.course.id,
        diploma_id: null,
      };
      this.favoriteCourseDiplomaService.Create(model).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.course.isFavorite = true;
          this.toastr.success("General.AddSuccessfully");
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
    }
  }

  addRequestDetails() {
    this.submitted = true;
    // console.log(this.myForm.value);
    if (this.myForm.valid) {
      let addRequestDetails: IAddCourseRequest = {
        courseId: this.course.id,
        diploma_id: null,
        reasonOfRequest: this.formCtrl?.reason.value,
        numberOfDays: this.formCtrl?.durationExpected?.value,
      };

      this.spinner.show();
      this.courseAndTeachingRequestService
        .addCourseRequest(addRequestDetails)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.toastr.success("General.AddSuccessfully");
            this.course.isPending = true;
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    }
  }

  ///////////////////// Course Content
  getAllStagesByCourseId() {
    this.spinner.show();
    this.subjectsService.getUnits(this.courseId).subscribe((res) => {
      if (res.length > 0) {
        this.units = res;
        this.units.forEach((unit, index) =>
          this.getAllUnitData(unit.id, index)
        );
      } else this.spinner.hide();
    });
  }

  // return data in stage (unit)
  getAllUnitData(unitId: number, index: number) {
    this.courseService.getAllUnitData(unitId).subscribe((res) => {
      if (res) {
        // exams & lectures
        let unitData: IUnitDataGeneralVM[] = res.model;
        unitData = unitData.sort((a, b) => a.order - b.order);
        // unitData.forEach((ele) => ele?.data?.sort((a, b) => a.order - b.order));
        this.units[index].data = unitData;
        // console.log(this.units[0]);
        this.spinner.hide();
      }
    });
  }
}
