import { Component, OnDestroy, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import {
  ICourseDetailsModel,
  IMyCoursesMV,
  ISimilarCourse,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { UserCourseService } from "app/student/services/user-course.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-my-course-completed",
  templateUrl: "./my-course-completed.component.html",
  styleUrls: ["./my-course-completed.component.scss"],
})
export class MyCourseCompletedComponent implements OnInit, OnDestroy {
  courseId: number;
  course: ICourseDetailsModel;
  today = new Date();
  courses: IMyCoursesMV[] = [];
  isLoadedCourses: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService,
    private spinner: NgxSpinnerService,
    public languageService: LanguageService,
    public appAssets: AppAssets,
    public fileService: UploadFileService,
    private userCourseService: UserCourseService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));
      if (this.courseId > 0) {
        this.getCourseDetails();
        this.getSimilarCourses(this.courseId);
      }
    });
  }

  ngOnInit() {}

  getCourseDetails() {
    this.spinner.show();
    this.courseService
      .getCourseDetails(this.courseId)
      .subscribe((res: ICourseDetailsModel) => {
        this.course = res;
        this.userCourseService.courseData$.next(res);
        this.spinner.hide();
      });
  }

  getSimilarCourses(courseId: number) {
    // this.spinner.show();
    this.courseService
      .getSimilarCourses(courseId)
      .subscribe((res: ISimilarCourse[]) => {
        res
          .map((ele) => {
            ele.subject_id = ele.course_id;
            ele.subject_ar_name = ele.course_ar_name;
            ele.subject_en_name = ele.course_en_name;
            ele.attach_path = ele.course_attach;
          })
          .splice(0, 2);
        this.courses = res;
        this.isLoadedCourses = true;
        // this.spinner.hide();
      });
  }

  ngOnDestroy(): void {
    this.userCourseService.courseData$.next(null);
    // this.userCourseService.courseData$.complete();
  }
}
